.about {
  display: flex;
  flex-direction: column;
  @include flexCenter(row, var(--clr-grey-light));
  @media only screen and (max-width: 537px){
    position: absolute;
    top: 385px;
  }
}

h5 {
  font-size: 2.5rem;
  margin-top: 1rem;
  color: var(--clr-orange-dark);
}

.about-card-deck {
  flex-direction: row;
  width: 100vw;
}

.about-card {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  margin: 0.4rem;
  z-index: -1;
}

.about-card:hover {
  background: var(--clr-orange-light);
  cursor: pointer;
  color: var(--clr-white);
}

.about-card-body {
  @include flexCenter(column, none) margin: 2rem;
}

.about-card-title {
  font-family: $fontFamily;
  margin-top: 0.8rem;
}

.about-card-title span {
  font-size: 1.5rem;
  font-weight: 900;
  margin-right: 0.5rem;
}

.about-card-text {
  font-family: $fontFamily;
  font-size: 12px;
  margin-bottom: 1rem;
  margin-top: 1rem;
}

.icons {
  margin-top: auto;
  font-size: 2rem;
}

